import {Badge, Button} from "react-bootstrap";
import {state} from "../store";
import {observer} from "mobx-react-lite";
import {openModal} from "../../../../common/component/modal/modalStore";
import {ChangePassword} from "./ChangePassword";
import {PaymentModal} from "./payment-modal/PaymentModal";
import {size} from "../../../../common/component/modal/IModal";
import {getTranslate} from "../../../../common/utils/localization";

export const Profile = observer(() => {
    return <div>
        <div className="d-block d-lg-flex justify-content-around">
            <div className="flex-fill">
                <div>
                    <span className="fw-bold-light">{getTranslate("Логин")}:</span> {state.state?.login}
                </div>

                <div>
                    <span className="fw-bold-light">{getTranslate("Электронная почта")}: </span>{state.state?.email}
                </div>
                <div>
                    <span className="fw-bold-light">{getTranslate("Дата регистрации")}: </span>{new Date(state.state?.registerDate).toLocaleDateString()}
                </div>
            </div>
            <div className="flex-fill">
                <div>
                    <span className="fw-bold-light">{getTranslate("Реферальные бонусы")}: </span>{state.state?.balance}
                </div>
                <div>
                    <span className="fw-bold-light">{getTranslate("Доступ")}: </span>

                    {new Date(state.state?.endAccessDate) > new Date() &&
                        <Badge bg="success">
                            {getTranslate("Активен до")} ({new Date(state.state?.endAccessDate).toLocaleDateString()})
                        </Badge>
                    }

                    {new Date(state.state?.endAccessDate) <= new Date() &&
                        <Badge bg="danger">
                            {getTranslate("Активен до")} ({new Date(state.state?.endAccessDate).toLocaleDateString()})
                        </Badge>
                    }
                </div>
            </div>
        </div>
        <div className="flex-column d-lg-flex justify-content-between mt-3">
            <div className="d-flex justify-content-around justify-content-lg-start">
                <Button variant="dark"
                        size="sm"
                        onClick={() => openModal({modal: <ChangePassword/>, size: undefined})}
                        className="me-2">
                    {getTranslate("Сменить пароль")}
                </Button>
                <Button variant="dark"
                        size="sm" onClick={() => openModal({modal: <PaymentModal/>, size: size.lg})}>
                    {getTranslate("Продлить подписку")}
                </Button>

            </div>
        </div>
        <hr/>
    </div>
})
