import React from "react";
import {Button} from "react-bootstrap";
import {useNavigate} from "react-router-dom";
import {getTranslate, localizationState} from "../../../../utils/localization";
import {state} from "../../../../appStore";
import {observer} from "mobx-react-lite";

export const Price = observer(() => {
    const navigation = useNavigate()


    return <div className="d-block d-lg-flex justify-content-around w-100 bg-second py-lg-5">
        <div className="d-flex flex-column flex-md-row justify-content-md-around justify-content-center align-items-center flex-fill my-3 my-lg-0">
            <div className="price-box py-2 my-3 my-md-0">
                <div className="pb-2">
                    <h3 className="fw-bold">{getTranslate("На 1 месяц")}</h3>
                </div>
                <div className="pb-2">
                    <div className="pb-2">200{getTranslate("руб/мес")}</div>
                    <Button className="bg-second-light w-75 py-3" onClick={() => navigation("/login")}>
                        <div>
                            {getTranslate("Подключить за ")}200{getTranslate("руб")}
                        </div>
                        {localizationState.lang !== "ru-ru" &&
                            <div>
                                (~{`${(200 / state.mainStore?.currencyList[0].value + 1.5).toFixed(1)} ${state.mainStore?.currencyList[0].charCode}`})
                            </div>
                        }
                    </Button>
                </div>
            </div>
            <div className="price-box py-2 my-3 my-md-0">
                <div className="pb-2">
                    <h3 className="fw-bold">{getTranslate("На 3 месяца")}</h3>
                </div>
                <div className="pb-2">
                    <div className="pb-2">180{getTranslate("руб/мес")}</div>
                    <Button className="bg-second-light w-75 py-3" onClick={() => navigation("/login")}>
                        {getTranslate("Подключить за ")}540{getTranslate("руб")}
                        {localizationState.lang !== "ru-ru" &&
                            <div>
                                (~{`${(540 / state.mainStore?.currencyList[0].value + 1.5).toFixed(1)} ${state.mainStore?.currencyList[0].charCode}`})
                            </div>
                        }
                    </Button>
                </div>
            </div>
        </div>
        <div className="d-flex flex-column flex-md-row justify-content-md-around justify-content-center align-items-center flex-fill my-3 my-lg-0">
            <div className="price-box py-2 my-3 my-md-0">
                <div className="pb-2">
                    <h3 className="fw-bold">{getTranslate("На 6 месяцев")}</h3>
                </div>
                <div className="pb-2">
                    <div className="pb-2">160{getTranslate("руб/мес")}</div>
                    <Button className="bg-second-light w-75 py-3" onClick={() => navigation("/login")}>
                        {getTranslate("Подключить за ")}960{getTranslate("руб")}
                        {localizationState.lang !== "ru-ru" &&
                            <div>
                                (~{`${(960 / state.mainStore?.currencyList[0].value + 1.5).toFixed(1)} ${state.mainStore?.currencyList[0].charCode}`})
                            </div>
                        }
                    </Button>
                </div>
            </div>
            <div className="price-box py-2 my-3 my-md-0">
                <div className="pb-2">
                    <h3 className="fw-bold">{getTranslate("На 1 год")}</h3>
                </div>
                <div className="pb-2">
                    <div className="pb-2">140{getTranslate("руб/мес")}</div>
                    <Button className="bg-second-light w-75 py-3" onClick={() => navigation("/login")}>
                        {getTranslate("Подключить за ")}1680{getTranslate("руб")}
                        {localizationState.lang !== "ru-ru" &&
                            <div>
                                (~{`${(1680 / state.mainStore?.currencyList[0].value + 1.5).toFixed(1)} ${state.mainStore?.currencyList[0].charCode}`})
                            </div>
                        }
                    </Button>
                </div>
            </div>
        </div>
    </div>
})